.gallery {
    background: -webkit-gradient(linear,
            left top,
            left bottom,
            color-stop(20%, var(--section-background-color)),
            color-stop(20%, var(--general-white)));
    background: -o-linear-gradient(top,
            var(--section-background-color) 20%,
            var(--general-white) 20%);
    background: linear-gradient(to bottom,
            var(--section-background-color) 20%,
            var(--general-white) 20%);
    padding-bottom: 60px;
    width: 100%;
}

.gallery-container {
    padding: 0 0px;
}

.gallery-row {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 3px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin: 0 auto;
}

.gallery-column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    gap: 3px;
}

.gallery-image {
    cursor: pointer;
    height: auto;
    max-width: 100%;
    overflow: hidden;
    -webkit-transition: -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    -o-transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
        -webkit-transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    width: auto;
}

.gallery-image:hover {
    -webkit-transform: scale(1.25);
    -ms-transform: scale(1.25);
    transform: scale(1.25);
    z-index: 2;
}

.gallery-column-one {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

.gallery-column-two {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    padding-top: 5px;
}

.gallery-column-three {
    -webkit-box-flex: 2;
    -ms-flex-positive: 2;
    flex-grow: 2;
}

.gallery-image-five {
    width: 50%;
}

.gallery-column-four {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    padding-top: 15px;
}

.gallery-column-five {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    padding-bottom: 45px;
}

.gallery-column-six {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    padding-top: 5px;
}

/* Fixed button */

.fixedbutton {
    border-radius: 50%;
    border: none;
    bottom: 27px;
    -webkit-box-shadow: 0 2px 4px var(--brand-color);
    box-shadow: 0 2px 4px var(--brand-color);
    display: none;
    height: 40px;
    position: fixed;
    right: 20px;
    -webkit-transition: -webkit-box-shadow 0.1s linear,
        -webkit-transform 0.1s linear;
    transition: -webkit-box-shadow 0.1s linear, -webkit-transform 0.1s linear;
    -o-transition: box-shadow 0.1s linear, transform 0.1s linear;
    transition: box-shadow 0.1s linear, transform 0.1s linear;
    transition: box-shadow 0.1s linear, transform 0.1s linear,
        -webkit-box-shadow 0.1s linear, -webkit-transform 0.1s linear;
    width: 40px;
    z-index: 3;
}

.fixedbutton:hover,
.fixedbutton:focus {
    border-radius: 35%;
    cursor: pointer;
    -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg);
}

.fixedbutton:active {
    border-radius: 35%;
    -webkit-box-shadow: 0 0 2px var(--brand-color);
    box-shadow: 0 0 2px var(--brand-color);
    -webkit-transform: translateY(2px) rotate(-90deg);
    -ms-transform: translateY(2px) rotate(-90deg);
    transform: translateY(2px) rotate(-90deg);
}

@media screen and (min-width: 768px) {
    .gallery {
        padding-bottom: 60px;
    }

    .gallery-container {
        padding: 0 60px;
    }
}

@media screen and (min-width: 1200px) {
    .gallery {
        padding-bottom: 120px;
        margin-top: -89px;
    }

    .gallery-container {
        max-width: 1200px;
        padding: 0 93px;
        margin: 0 auto;
    }
}